import { Component, OnInit } from '@angular/core';
import { Validators, FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { ProductService } from '../../services/product.service';
import { Prima } from '../../models/prima.model';

import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/of';

@Component({
  selector: 'primasView',
  templateUrl: './primas.component.html',
  styleUrls: ['./primas.component.css'],
  providers: [ ProductService ],
})

export class PrimasComponent{
  loading: Boolean;
  loadingForm:Boolean;
  nuevaPrima = new Prima();
  productos: any[];
  resultado: Observable<any[]>;
  resultado2: Observable<Boolean>;
  errorProductos: Boolean;
  errorProducto: Boolean;
  productoAniadido: Boolean;
  editMode: Boolean;
  //Ventana modal
  modalActivado:Boolean;
  moda2lActivado:Boolean;
  selectedP = new Prima();
  //Form
  addForm: FormGroup;
  modForm: FormGroup;
  constructor(private productService:ProductService, private formBuilder: FormBuilder) {
    this.loading = false;
    this.loadingForm = false;
    this.errorProductos = false;
    this.errorProducto = false;
    this.modalActivado = false;
    this.moda2lActivado = false;
    this.productoAniadido = false;
    this.editMode = false;

  }

  ngOnInit() {
    this.productos = [];
    this.loading = true;
    this.resultado = this.productService.misProductos().catch(error => {
      console.log("Error al acceder a los productos");
      this.errorProductos = true;
    return Observable.of<any[]>(null);
    });

    this.resultado.subscribe(val => {
      this.productos = val;
      console.log(val);
      this.loading = false;
    });

    this.createForm();
    this.createForm2();
  }

  modalClick(){
    if(this.modalActivado == false){
      this.modalActivado = true;
    }else{
      this.modalActivado = false;
    }
  }

  modal2Click(prim){
    if(this.moda2lActivado == false){
      this.selectedP = prim;
      this.nuevaPrima.nombreNew = this.selectedP.nombre;
      this.moda2lActivado = true;
    }else{
      this.moda2lActivado = false;
    }
  }

  onSubmit(): void{
    this.loadingForm = true;
    this.nuevaPrima.nombre = this.addForm.value.nombre;
    this.nuevaPrima.departamento = this.addForm.value.departamento;
    this.nuevaPrima.cantidad = this.addForm.value.cantidad;
    this.nuevaPrima.unidad = this.addForm.value.unidad;
    this.nuevaPrima.precio = this.addForm.value.precio;
    this.nuevaPrima.fecha = this.addForm.value.fecha;

    this.productService.agregarPrima(this.nuevaPrima).subscribe(val => {
      this.productoAniadido = val;
      if(val){
        this.modalClick();
      }
      this.loadingForm = false;
      this.errorProducto = !val;
      this.ngOnInit();
    });
  }


    modPrima(){
      this.loadingForm = true;
      this.nuevaPrima.nombre = this.modForm.value.nombre;
      this.nuevaPrima.departamento = this.modForm.value.departamento;
      this.nuevaPrima.cantidad = this.modForm.value.cantidad;
      this.nuevaPrima.unidad = this.modForm.value.unidad;
      this.nuevaPrima.precio = this.modForm.value.precio;
      this.nuevaPrima.fecha = this.modForm.value.fecha;
      console.log(this.nuevaPrima);
      this.productService.modificarPrima(this.nuevaPrima).subscribe(val => {
        this.productoAniadido = val;
        if(val){
          this.modal2Click("Cerro");
        }
        this.loadingForm = false;
        this.errorProducto = !val;
        this.ngOnInit();
      });
    }

  deletePrima(nombre){
    this.productService.deletePrima(nombre).subscribe(val => {
      if(val){
        console.log("Exito al eliminar el producto")
      }
      this.errorProducto = !val;
      this.ngOnInit();
    });
  }


  createForm(){
    this.addForm = this.formBuilder.group({
      nombre: ['', Validators.required ],
      departamento: ['', Validators.required ],
      cantidad: ['', [Validators.required, Validators.min(0)]],
      unidad: ['', Validators.required ],
      precio: ['', [Validators.required, Validators.min(0)]],
      fecha: ['', []],
    });
  }

  createForm2(){
    this.modForm = this.formBuilder.group({
      nombre: ['', [] ],
      departamento: ['', Validators.required ],
      cantidad: ['', [Validators.min(0)]],
      unidad: ['', Validators.required ],
      precio: ['', [Validators.min(0)]],
      fecha: ['', []],
    });
  }

  changeEditMode(){
    if(this.editMode == false){
      this.editMode = true;
    }else{
      this.editMode = false;
    }
  }

}



