import { Component, OnInit } from '@angular/core';
import { Validators, FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { ProductService } from '../../services/product.service';
import { Receta } from '../../models/receta.model';

import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/of';

@Component({
  selector: 'recetasView',
  templateUrl: './recetas.component.html',
  styleUrls: ['./recetas.component.css'],
  providers: [ ProductService ],
})

export class RecetasComponent{
  resProducto: Observable<any[]>;
  productos: any[];
  nuevaReceta = new Receta();
  loading: Boolean;
  loadingForm:Boolean;
  errorRecetas: Boolean;
  errorReceta: Boolean;
  recetas: any[];
  resultado: Observable<any[]>;
  recetaAniadido: Boolean;
  //Ventana modal
  modalActivado:Boolean;
  //Form
  addForm: FormGroup;
  constructor(private productService:ProductService, private formBuilder: FormBuilder) {
    this.loading = false;
    this.loadingForm = false;
    this.modalActivado = false;
    this.recetaAniadido = false;
    this.errorRecetas = false;
    this.errorReceta = false;
  }

  ngOnInit() {
    this.loading = true;
    this.recetas = [];
    this.resultado = this.productService.getRecetas().catch(error => {
      console.log("Error al acceder a los producos");
      this.errorRecetas = true;
    return Observable.of<any[]>(null);
    });

    this.resultado.subscribe(val => {
      this.recetas = val;
      this.loading = false;
      console.log(this.recetas);
    });
/*
    for(var i=0;i < this.recetas.length; i++){
      this.recetas[i]._fields[0].properties.cantidad = this.recetas[0]._fields[0].properties.cantidad.split(',');
      this.recetas[i]._fields[0].properties.ingrediente = this.recetas[0]._fields[0].properties.ingrediente.split(',');
    }
*/
      this.createForm();
  }

  onSubmit(): void{
    this.loadingForm = true;
    this.nuevaReceta.nombre = this.addForm.value.nombre;
    this.nuevaReceta.ingrediente = [this.addForm.value.ingrediente];
    this.nuevaReceta.cantidad = [this.addForm.value.cantidad];
    console.log(this.nuevaReceta);
    this.productService.agregarReceta(this.nuevaReceta).subscribe(val => {
      this.recetaAniadido = val;
      if(val){
        console.log("exito")
        //this.modalClick();
      }
      this.loadingForm = false;
      this.errorReceta = !val;
      this.ngOnInit();
    });

    this.modalClick();
  }


  deleteReceta(nombre){
    this.productService.deleteReceta(nombre).subscribe(val => {
      if(val){
        console.log("exito")
        //this.modalClick();
      }
      this.loadingForm = false;
      this.errorReceta = !val;
      this.ngOnInit();
    });
  }

  createForm(){
    this.addForm = this.formBuilder.group({
      nombre: ['', Validators.required ],
      ingrediente: ['', Validators.required ],
      cantidad: ['', [Validators.required, Validators.min(0)]],
    });
  }

  modalClick(){
    if(this.modalActivado == false){
      this.productos = [];
      this.resProducto = this.productService.misProductos().catch(error => {
        console.log("Error al acceder a los producos");
        this.errorRecetas = true;
      return Observable.of<any[]>(null);
      });

      this.resProducto.subscribe(val => {
        this.productos = val;
        this.loading = false;
      });
      this.modalActivado = true;
    }else{
      this.modalActivado = false;
    }
  }

}



