import { NgModule }              from '@angular/core';
import { RouterModule, Routes }  from '@angular/router';

//Componentes Unicos
import { HomeComponent } from './components/home/home.component';
import { ConsultasComponent } from './components/consultas/consultas.component';
import { PrimasComponent } from './components/primas/primas.component';
import { ProductosComponent } from './components/productos/productos.component';
import { RecetasComponent } from './components/recetas/recetas.component';

//Componentes Semi-globales
import { MenuComponent } from './components/menu/menu.component';
import { NotFoundComponent } from './components/notFound/notFound.component';

const appRoutes: Routes = [
  { path: "", component: HomeComponent, data: { header: true}},
  { path: "inicio", redirectTo: "", pathMatch: "full" },
  { path: "home", redirectTo: "", pathMatch: "full" },
  { path: "mPrima", component: PrimasComponent, data: { header: true}},
  { path: "recetas", component: RecetasComponent, data: { header: true}},
  { path: "productos", component: ProductosComponent, data: { header: true}},
  { path: "consulta", component: ConsultasComponent, data: { header: true}},
  { path: "**", component: NotFoundComponent, data: { header: false}},
];

@NgModule({
  imports: [
    RouterModule.forRoot(
      appRoutes
       //{ enableTracing: true }  <-- debugging purposes only
    )
  ],
  exports: [
    RouterModule
  ]
})
export class AppRoutingModule {}



