import { Injectable } from '@angular/core';
import { Http, Headers, RequestOptions, Response } from '@angular/http';
import { Prima } from '../models/prima.model';
import { Receta } from '../models/receta.model';

import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map'

@Injectable()
export class ProductService {
  private prima: Prima;
  private receta: Receta;
  private producto:any;
  private recetas:any;
  private productoFinal:any;
  private productoObservable: Observable<any>;
  private productoFinalObservable: Observable<any>;

  constructor(private http: Http) {
    this.prima = new Prima();
    this.receta = new Receta();
  }

  misProductos(){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });

    this.productoObservable = this.http.get('http://localhost:3000/', options)
    .map((response: Response) => {
      //console.log(response.json());
    if (response.status == 200) {
       this.producto = response.json();
        return this.producto;
      } else {
        return undefined;
      }
    });
    return this.productoObservable;
  }

  agregarPrima(prima: Prima): Observable<Boolean> {
    let headers = new Headers({ 'Content-Type': 'application/json' });
    //console.log(prima);
    return this.http.post('http://localhost:3000/addPrima',JSON.stringify(prima),{headers: headers})
    .map((response: Response) => {
      if (response.status == 200) {
          //console.log(response._body);
          return true;
      } else {
        return false;
      }
    });
  }

  modificarPrima(prima: Prima): Observable<Boolean> {
    let headers = new Headers({ 'Content-Type': 'application/json' });
    console.log(prima.nombreNew);
    return this.http.post('http://localhost:3000/modPrima',JSON.stringify(prima),{headers: headers})
    .map((response: Response) => {
      if (response.status == 200) {
          //console.log(response._body);
          return true;
      } else {
        return false;
      }
    });
  }

  deletePrima(nombre:string){
    let miReceta = {"nombre":nombre};
    //console.log(miReceta)
    let headers = new Headers({ 'Content-Type': 'application/json' });
    return this.http.put('http://localhost:3000/deleteProducto',JSON.stringify(miReceta),{headers: headers})
    .map((response: Response) => {
      //console.log(response.status);
      if (response.status == 200) {
          return true;
      } else {
        return false;
      }
    });
  }

  getRecetas(){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });

    this.productoObservable = this.http.get('http://localhost:3000/recetas', options)
    .map((response: Response) => {
    if (response.status == 200) {
        this.recetas = response.json();
        console.log(response.json());
        return this.recetas;
      } else {
        return undefined;
      }
    });
    return this.productoObservable;
  }

  agregarReceta(receta: Receta){
    let recetaPrueb = {"nombre":"Puerco a la Manzana", "ingrediente":["Puerco","Manzana"], "cantidad":["12","32.2"]};
    let headers = new Headers({ 'Content-Type': 'application/json' });
    return this.http.post('http://localhost:3000/agregarReceta',JSON.stringify(receta),{headers: headers})
    .map((response: Response) => {
      console.log(response.status);
      if (response.status == 200) {
          return true;
      } else {
        return false;
      }
    });
  }

  deleteReceta(nombre:string){
    let miReceta = {"nombre":nombre};
    let headers = new Headers({ 'Content-Type': 'application/json' });
    return this.http.put('http://localhost:3000/deleteReceta',JSON.stringify(miReceta),{headers: headers})
    .map((response: Response) => {
      console.log(response.status);
      if (response.status == 200) {
          return true;
      } else {
        return false;
      }
    });
  }

  getProductoFinal(){
    let headers = new Headers({ 'Content-Type': 'application/json' });
    this.productoObservable = this.http.get('http://localhost:3000/productoElaborados',{headers: headers})
    .map((response: Response) => {
    if (response.status == 200) {
        this.productoFinal = response.json();
        return this.productoFinal;
      } else {
        return undefined;
      }
    });
    return this.productoObservable;
  }

  deleteProductoFinal(nombre:string){
    let miProducto = {"nombre":nombre};
    console.log(miProducto);
    let headers = new Headers({ 'Content-Type': 'application/json' });
    return this.http.put('http://localhost:3000/eliminarProductoElaborados',JSON.stringify(miProducto),{headers: headers})
    .map((response: Response) => {
      console.log(response.status);
      if (response.status == 200) {
          return true;
      } else {
        return false;
      }
    });

  }

  elaborarProducto(nombre:string){
    let miProducto = {"nombre":nombre};
  }

}



