export class Prima{
  public nombre: string;
  public nombreNew: string;
  public departamento: string;
  public cantidad: number;
  public unidad: string;
  public precio: number;
  public fecha: string;
  constructor(){
    this.nombre = "";
    this.nombreNew = "";
    this.departamento = "";
    this.cantidad = -1;
    this.unidad = "";
    this.precio = -1;
    this.fecha = "";
  }
};



